@import 'src/common/styles/sass/variables';
@import 'src/common/styles/sass/mixins';

.carousel {
  width: 100%;
  position: relative;
  min-height: 45vh;

  .container {
    width: 100%;
    min-height: 45vh;
    background: $primary-color;
    display: flex;
    justify-content: flex-end;
    position: relative;

    .carousel_background__image__building {

      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      clip-path: polygon(40% 0%, 100% 0%, 100% 50%, 100% 100%, 40% 100%, 50% 50%);
      @media screen and (max-width: 1350px) {
        clip-path: polygon(55% 0%, 100% 0%, 100% 50%, 100% 100%, 65% 100%, 85% 50%);
      }
      @media screen and (max-width: 950px) {
        clip-path: polygon(75% 0%, 100% 0%, 100% 50%, 100% 100%, 75% 100%, 85% 50%);
      }
      @media screen and (max-width: 750px) {
        display: none;
      }
    }

    .carousel_background__image__women {
      position: absolute;
      bottom: 0;
      width: 600px;
      left: 51%;
      overflow: auto;
      @media screen and (max-width: 1350px) {
        right: -10%;
        left: auto;
        @media screen and (max-width: 950px) {
         width: 500px;
        }
      }
    }
  }

  .section {

    position: absolute;
    top: 20%;
    left: 22%;

    max-width: 500px;
    z-index: 1;
    @include flex(flex, flex-start, flex-start, column);
    gap: 40px;
    @media screen and (max-width: 1350px) {
      left: 12%;
    }
    @media screen and (max-width: 950px) {
      top: 30%;
      left: 5%;
    }
    @media screen and (max-width: 750px) {
      top: 10%;
    gap: 150px;

    }

    .carousel__title {
      @include font($secondary-font-family, 700, 36px, 42px, $secondary-font-color);
      @media screen and (max-width: 950px) {
        font-size: 30px;
        line-height: 35px
      }
    }
  }
}

