@import 'src/common/styles/sass/variables';
@import 'src/common/styles/sass/mixins';

.burger_icon {

  .btn {
    display: flex;
    background-color: transparent;
    border: none;
    z-index: 6;
    position: absolute;
    right: 40px;
    top: 30px;
  }
}

.burger_menu_content {
  z-index: 5;
  top: 0;
  right: 0;
  left: 0;

  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: $primary-font-color;
  text-align: center;
  position: absolute;
  width: 100%;
  min-height: 60vh;


  opacity: 0;

  overflow: hidden;


  .burger_links__container {
   margin-top: 100px;
    width: 80%;

    gap: 20px;
    list-style-type: none;
    height: 400px;
    font-size: 18px;

    .burger_links__list {

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 40px;
      justify-content: flex-start;
      list-style-type: none;
      li {
        @include font($secondary-font-family, 400, 16px, 19px);
        color: #FFFFFF;
        &:hover {
          color: #E9862A;
          font-weight: 700;
        }
      }
    }
  }
}

.burger_show {
  opacity: 1;
  max-height: 400px;
}




.dropdown {

  position: absolute;
}

.dropdown_block {
  @include flex(flex, center, center);
  position: absolute;

  width: 173px;
  height: 137px;
  list-style-type: none;
}

.social {
  @include flex(flex, center, center);
  width: 68px;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.08);;
}